:root {
  --primary-color: #120B48;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.popup-wrapper {
  border-radius: 20px;
  padding: 16px;
  min-width: 250px;
  position: relative;
}

.popup-header {
  display: flex; 
  justify-content: space-between;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
}

.popup-logo-text {
  color: var(--primary-color);
  font-weight: bolder;
}

.popup-header--icons {
  display: flex;
  gap: 10px;
}

.popup-wrapper > p {
  margin-top: 16px;
}

.tab-selectors {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
  margin-top: 24px;
  /* text-align: center; */
}

.tab-selector {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.first-tab p {
  opacity: .4;
}

.tab-selector img {
  width: 30px;
}

.tab-selector p {
  font-weight: bold;
  color: var(--primary-color);
  margin-top: 6px;
}

.toggle-permission {
  border: 1px solid var(--primary-color);
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  margin-top: 24px;
}

.toggle-desc {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 6px;
}

.toggle-slider {
  background: var(--primary-color);
  width: 40px;
  height: 20px;
  border-radius: 100px;
}

.toggle-circle {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: white;
  margin-left: 20px;
  margin-top: 1px;
}

.popup-btn {
  width: 100%;
  background: var(--primary-color);
  color: white;
  border-radius: 6px;
  padding: 12px 10px;
  border: none;
  font-weight: bold;
  margin-top: 24px;
  cursor: pointer;
}

#video {
  border: 1px solid gold;
}

.screen-ctrls {
  background: rgb(19, 18, 18);
  max-width: 400px;
  color: white;
  /* position: absolute;
  cursor: move; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 50px;
  padding: 5px 24px;
  gap: 5px;
  z-index: 300;
  /* left: 130px;
  top: 80%; */
}

.ctrls {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 4px 0;
}

.ctrls img {
  width: 35px;
  cursor: pointer;
}

.screen-ctrls--divider {
  /* width: px; */
  height: 50px;
  border: 1px solid rgb(219, 187, 187);
}

.ctrls button {
  background: transparent;
  border: none;
}

.screen-ctrls--trash {
  width: 40px;
}


